.services {
  &-swiper {
    width: 100%;
    max-width: 100%;
    height: 100%;
    margin-bottom: 70px;

    &-slide {
      display: flex;
      flex-direction: column;
      max-width: 270px;
    }

    &__title {
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      color: var(--green-color);
      margin-bottom: 10px;
    }

    &__btn {
      font-size: 16px;
      line-height: 21px;
      color: var(--green-color);
      background: none;
      border: none;
      outline: none;
      border: 1px solid var(--green-color);
      border-radius: 5px;
      height: 30px;
      margin-top: 25px;
      align-self: flex-end;
      cursor: pointer;
    }
  }

  &-up {
    @include flex-all-center;
    justify-content: start;
    margin-bottom: 40px;
  }

  &__btn {
    margin-left: 10px;
    width: 227px;
    height: 37px;
    font-size: 16px;
    line-height: 18px;
    color: var(--red-color);
    background: none;
    border: none;
    outline: none;
    border: 1px solid var(--red-color);
    border-radius: 5px;
    cursor: pointer;
  }


  &-bottom {
    width: 100%;
    padding: 40px 0;
    @include flex-all-center;
    background-color: var(--green-color);


    &__text {
      max-width: 1070px;
      font-size: 24px;
      line-height: 31px;
      text-align: center;
      color: var(--light-color);
    }
  }
}
