:root {
  --font-family: "Mansory", sans-serif;
  --content-width: 1260px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  --light-color: #ffffff;
  --red-color: #b7171f;
  --red-color-op: rgba(183, 23, 31, 0.7);
  --green-color: #0f5534;
  --dark-color: #130001;
}
