.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

a {
  text-decoration: none;
}

span {
  display: inline-block;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  transition: opacity 0.8s ease-in-out;
  background-color: var(--light-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader.is-active {
  opacity: 0;
}

.loader img {
  width: 130px;
  height: 130px;
  animation: loader infinite 1.5s;
}

.title {
  font-weight: 500;
  font-size: 36px;
  line-height: 47px;
  text-transform: uppercase;
  color: var(--dark-color);
}

.text {
  font-size: 18px;
  line-height: 23px;
  color: var(--dark-color);
}

@keyframes loader {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.header {
  padding: 15px 0;
  position: fixed;
  z-index: 99;
  width: 100%;
  background-color: var(--light-color);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-list {
  display: flex;
  align-items: center;
}
.header-list-item:not(:last-child) {
  margin-right: 30px;
}
.header-list__item {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: var(--dark-color);
  transition: color 0.3s ease-in-out;
}
.header-list__item:hover {
  color: var(--red-color);
}
.header-list__item:focus {
  border-bottom: 1px solid var(--red-color-op);
}
.header-list__item:active {
  color: var(--red-color);
}
.header-social {
  display: flex;
  align-items: center;
}
.header-social-item {
  max-width: 185px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  align-items: center;
}
.header-social-item:not(:last-child) {
  margin-right: 29px;
}
.header-social-item__text {
  font-weight: 700;
  font-size: 11px;
  line-height: 14px;
  color: var(--green-color);
  padding-left: 10px;
}
.header-social-item-phones {
  display: flex;
  flex-direction: column;
}
.header-social-item__icon {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 26px;
  height: 24px;
}
.header-social-item__icon:nth-child(1) {
  background-image: url(../img/clock.svg);
}
.header-social-item__icon:nth-child(2) {
  background-image: url(../img/map.svg);
}
.header-social-item__icon:nth-child(3) {
  background-image: url(../img/phone.svg);
}

.hero {
  padding: 70px 0;
