.work {
  padding: 70px 0;

  &-container {
    margin-top: 70px;
  }

  &-swiper-1 {
    width: 100%;
    height: 100%;
  }

  &-swiper-2 {
    width: 100%;
    height: 100%;

    &-wrapper {
      max-width: 1170px;
      margin: 0 auto;
    }

    &-slide {
      display: flex;
      justify-content: space-between;

      &-left {
        background-color: var(--green-color);
      }

      &__text {
        font-weight: 500;
        font-size: 24px;
        line-height: 31px;
        color: var(--light-color);
      }

      &__author {
        font-weight: 700;
        font-size: 18px;
        line-height: 23px;
        color: var(--light-color);
      }

      &__pets {
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        color: var(--light-color);
      }
    }
  }
}

.swiper-button-prev:after, .swiper-rtl .swiper-button-next:after {
  content: '';
}

.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after {
  content: '';
}
