.header {
  padding: 15px 0;
  position: fixed;
  z-index: 99;
  width: 100%;
  background-color: var(--light-color);

  &__container {
    @include flex-all-sb;
  }

  &-list {
    @include flex-v-center;

    &-item {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    &__item {
      font-weight: 500;
      font-size: 14px;
      line-height: 18px;
      color: var(--dark-color);
      transition: color .3s ease-in-out;

      &:hover {
        color: var(--red-color);
      }

      &:focus {
        border-bottom: 1px solid var(--red-color-op);
      }

      &:active {
        color: var(--red-color);
      }
    }
  }

  &-social {
    @include flex-v-center;

    &-item {
      max-width: 185px;
      width: 100%;
      @include flex-all-center;

      &:not(:last-child) {
        margin-right: 29px;
      }

      @include flex-v-center;

      &__text {
        font-weight: 700;
        font-size: 11px;
        line-height: 14px;
        color: var(--green-color);
        padding-left: 10px;
      }

      &-phones {
        display: flex;
        flex-direction: column
      }

      &__icon {
        @include bg-cover;
        width: 26px;
        height: 24px;

        &:nth-child(1) {
          background-image: url(../img/clock.svg);
        }

        &:nth-child(2) {
          background-image: url(../img/map.svg);
        }

        &:nth-child(3) {
          background-image: url(../img/phone.svg);
        }
      }
    }
  }
}
