.choice {
  padding: 70px 0;

  &__title {
    margin-bottom: 50px;
  }

  &-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
    gap: 30px;

    &-item {
      display: flex;
      align-items: start;
      justify-content: center;

      &-group {
        margin-left: 20px;
      }

      &__img {
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        width: 44px;
        height: 44px;

        &--1 {
          background-image: url(../img/bandaid.svg);
        }

        &--2 {
          background-image: url(../img/stars.svg);
        }

        &--3 {
          background-image: url(../img/person-check.svg);
        }

        &--4 {
          background-image: url(../img/award.svg);
        }

        &--5 {
          background-image: url(../img/house.svg);
        }

        &--6 {
          background-image: url(../img/card-checklist.svg);
        }
      }

      &__title {
        font-weight: 500;
        font-size: 24px;
        line-height: 31px;
        color: var(--red-color);
        margin-bottom: 15px;
      }

      &__text {
        font-size: 14px;
        line-height: 18px;
        max-width: 570px;
      }
    }
  }

  &-red {
    padding: 40px 0;
    background-color: var(--red-color);

    &-block {
      font-size: 36px;
      line-height: 47px;
      text-align: center;
      color: var(--light-color);
    }
  }
}
