.hero {
  padding: 70px 0;
  &-container {
    min-height: 600px;
    height: 100%;
    background-repeat: no-repeat;
    background-size: auto;
    background-position: right center;
    background-image: url(../img/hero.jpg);
    display: flex;
    align-items: center;
  }

  &__wrapper {
    max-width: 570px;
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  &-title {
    font-weight: 500;
    font-size: 48px;
    line-height: 62px;
    text-transform: uppercase;
    color: var(--dark-color);
    margin-bottom: 20px;
  }

  &__btn {
    margin-top: 20px;
    padding: 20px 0;
    max-width: 230px;
    width: 100%;
    background: var(--red-color);
    outline: none;
    border: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: var(--light-color);
    border-radius: 5px;
    cursor: pointer;
  }
}
